import React, { useState, useEffect } from 'react'
import PeopleForm from './components/PeopleForm';
import People from './components/People'
import personService from './services/people';
import SearchBox from './components/SearchBox';
import Notification from './components/Notification';

const App = () => {
  const [people, setPeople] = useState([]) 
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [showAll, setShowAll] = useState(true)
  const [searchPerson, setSearchPerson]  = useState('')
  const [filteredPeople, setFilteredPeople] = useState([])
  const [isUpdated, setUpdated] = useState(false)
  const [notification, setNotification] = useState(null)

  useEffect(() => {
    personService
      .getAll()
      .then(data => {
        setPeople(data)
      })
  }, [])

  const notifyWith = (message, type='success') => {
    setNotification({ message, type })
    setTimeout(() => {
      setNotification(null)
    }, 5000)
  }

  const peopleToShow = showAll
  ? people
  : filteredPeople

  const handlePersonSearch = (event) => {
    let searchPerson = event.target.value
    let filteredPeople = people.filter(person => {
      return person.name.toLowerCase().includes(searchPerson.toLowerCase())
    })
    setSearchPerson(searchPerson)
    if (searchPerson === '') {
      setShowAll(peopleToShow)
    } else {
      setFilteredPeople(filteredPeople)
      setShowAll(!showAll)
    }
  }

  const addPerson = (event) => {
    event.preventDefault()
    const personObject = {
      name: newName,
      number: newNumber
    }
    const namesOfPeopleInPhonebook = people.map((person) => {
      return person.name
    })
    namesOfPeopleInPhonebook.includes(newName) // && !people.includes(newNumber)
    ?
    (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)
    && handleSetUpdated()
    )
    //alert(`${newName} is already added to the phonebook`)
    :
    personService
    .create(personObject)
    .then(returnedPerson => {
      setPeople(people.concat(returnedPerson))
      setNewName('')
      setNewNumber('')
      notifyWith(`Added ${newName}`)
    })
    .catch(err => {
      notifyWith(`${err.response.data.err} `, 'error')
    })
  }

  // TODO: after update one shouldn't need to refresh the page to get other people's contact info back
  const updatePerson = (id, newNumber) => {
    const person = peopleToShow.find(p => p.id === id)
    const changedPerson = { ...person, number: newNumber }
    personService
    .update(id, changedPerson)
    .then(returnedPerson => {
      setPeople(people.map(person => person.id === id && returnedPerson))
      notifyWith(
        `Number was updated`
      )
    })
  }

  const deletePerson = (id) => {
    const toDelete = people.find(p => p.id === id)
    const ok = window.confirm(`Delete ${toDelete.name}`)
    if (ok) {
      personService
      .del(id)
      .then(() => {
        setPeople(people.filter(p => p.id !== id))
        notifyWith(`Deleted ${toDelete.name}`)
      })
      .catch(() => {
        setPeople(people.filter(p => p.id !== id))         
        notifyWith(`${toDelete.name} had already been removed`, 'error')
      })
    }
  }

  const handlePersonChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleSetUpdated = () => {
    const person = peopleToShow.find(person => person.name === newName)
    const id = person.id
  
    setUpdated(true)
    updatePerson(id, newNumber)
  }

  return (
    <div className="app">
      <h1>Phonebook</h1>
      <Notification notification={notification} />
      <SearchBox 
        searchPerson={searchPerson}
        handlePersonSearch={handlePersonSearch}
      />
      <h2>add a new</h2>
      <PeopleForm 
        addPerson={addPerson}
        updatePerson={updatePerson}
        newName={newName}
        newNumber={newNumber}
        handleNumberChange={handleNumberChange}
        handlePersonChange={handlePersonChange}
        peopleToShow={peopleToShow}
        isUpdated={isUpdated}
        handleSetUpdated={handleSetUpdated}
      />
      <h2>Numbers</h2>
      <People 
        peopleToShow={peopleToShow}
        deletePerson={deletePerson}
      />
    </div>
  )
}

export default App